import {lookup} from '../lookup'

export function loadingPosts(username,callback){ //postslist
  let endpoint =  "/posts/" //leave like this, not if else
  if (username){
      endpoint =  `/posts/?username=${username}`
  }
  lookup("GET", endpoint, callback)
   }
 
export function detailView(id,callback){ //postslist

    lookup('GET',`/posts/${id}/`,callback)
  
     }

 export function createPost(newPost,callback){
   lookup('POST', '/posts/create', callback, {content:newPost})
 }
 
 export function likePost(id, action, callback){
   lookup('POST', '/posts/action', callback,{id:id, action:action})
 }